.vertical {
   position: absolute;
   width: 0px;
   height: 1080px;
   left: 1040px;
   top: 0px;
   opacity: 0.1;
   border: 1px solid #42567a;
}

.horizontal {
   position: absolute;
   width: 1440px;
   height: 0px;
   left: 320px;
   top: 480px;
   opacity: 0.1;
   border: 1px solid #42567a;
}

.circle {
   box-sizing: border-box;
   position: absolute;
   width: 530px;
   height: 530px;
   left: 775px;
   top: 215px;
   opacity: 0.2;
   border: 1px solid #42567a;
   border-radius: 50%;
}

.gradient {
   position: absolute;
   width: 5px;
   height: 120px;
   left: 322px;
   top: 177px;
   background: linear-gradient(0deg, #ef5da8, #3877ee);
}

.button_right:hover {
